  */
  max-height: 88px !important;
}

.m-notification {
  display: none;
  position: relative;
  padding: 15px;
  background: var(--gray-5);
  border: 1px solid var(--gray-40);
}
.m-notification > .cf-icon-svg {
  position: absolute;
  fill: var(--gray);
}
.m-notification--success {
  background: var(--green-10);
  border-color: var(--green);
}
.m-notification--success > .cf-icon-svg {
  fill: var(--green);
}
.m-notification--warning {
  background: var(--gold-10);
  border-color: var(--gold);
}
.m-notification--warning > .cf-icon-svg {
  fill: var(--gold);
}
.m-notification--error {
  background: var(--red-10);
  border-color: var(--red);
}
.m-notification--error > .cf-icon-svg {
  fill: var(--red);
}
.m-notification--success a, .m-notification--warning a, .m-notification--error a {
  color: var(--pacific-mid-dark);
  border-color: var(--pacific-mid-dark);
}
.m-notification--success a:visited, .m-notification--success a.visited, .m-notification--warning a:visited, .m-notification--warning a.visited, .m-notification--error a:visited, .m-notification--error a.visited {
  border-color: var(--teal);
  color: var(--teal);
}
.m-notification--success a:hover, .m-notification--success a.hover, .m-notification--warning a:hover, .m-notification--warning a.hover, .m-notification--error a:hover, .m-notification--error a.hover {
  border-color: var(--pacific-dark);
  color: var(--pacific-dark);
}
.m-notification--success a:focus, .m-notification--success a.focus, .m-notification--warning a:focus, .m-notification--warning a.focus, .m-notification--error a:focus, .m-notification--error a.focus {
  border-color: var(--pacific-mid-dark);
  color: var(--pacific-mid-dark);
}
.m-notification--success a:active, .m-notification--success a.active, .m-notification--warning a:active, .m-notification--warning a.active, .m-notification--error a:active, .m-notification--error a.active {
  border-color: var(--navy-dark);
  color: var(--navy-dark);
}
.m-notification--visible {
  display: block;
}
.cf-icon-svg + .m-notification__content {
  padding-left: 1.5625rem;
}
.m-notification__message {
  font-size: 1rem;
  line-height: 1.25;
  font-weight: 500;
}
@media only all and (min-width: 37.5625em) {
  .m-notification__message {
    font-size: 1.125rem;
  }
}
.m-notification__explanation {
  margin-top: 0.3125rem;
  margin-bottom: 0.9375rem;
}
.m-notification__message + .m-list {
  margin-top: 0.9375rem;
}
@media only all and (min-width: 37.5625em) {
  .m-notification .cf-icon-svg {
    font-size: 1.125rem;
  }
  .m-notification__explanation {
    margin-bottom: 0;
  }
  .m-notification .m-list {
    margin-top: 0.3125rem;
